.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.board-row:after {
  clear: both;
  content: "";
  display: table;

}
.square {
  background: #282c34;
  
  border: 1px solid #999;
  float: left;
  font-size: 18px;
  line-height: 34px;
  height: 60px;
  margin: 0px;
  padding: 0;
  text-align: center;
  width: 60px;
}
.status{
  font-size: 14px;  
}
.status> button{
  background-color: darkgreen;
  color: white;
  border-color:darkgreen;
  padding: 8px;
  margin: 8px;
}
.square:focus {
  outline: none;
}
.disable-select {
  -webkit-user-select: none;  
  -moz-user-select: none;    
  -ms-user-select: none;      
  user-select: none;
}

.history-button-link{
  background-color:'transparent !important'; 
  color:'#fff';
  text-decoration:'underline';
  cursor:'pointer';
  border:'none';
}

.history-section{
  border-style: dotted;
  height: 150px;
  overflow: auto;
}